.pager {
  &__items {
    list-style: none;
    padding-left: 0;
  }
  // Pager list item styles
  &__item {
    display: inline-block;
    margin-right: rhythm(.1);

    // Link styles
    a {
      @include adjust-font-size-to($label, 0.8);
      display: block;
      padding: rhythm(.1) rhythm(.4);
    }

    // Active page
    &.is-active a {
      font-weight: bold;
    }
  }
}
