.site-header {
  background-color: $white;
  position: relative;
  box-shadow: $shadow;
  z-index: 10;
}

.logo {
  align-self: flex-end;
  margin-bottom: 0.75em;
}

.site-header-container {
  display: flex;
}

.nav {
  list-style-type: none;
  padding-left: none;
  margin: 0;
  li {
    display: inline-block;
    margin: 0 $space-half;
    &:last-child {
      margin-right: 0;
    }
    a {
      display: inline-block;
      text-decoration: none;
      // font-family: $serif;
      color: $navy;
      font-weight: 700;
      font-size: 1.3125rem;
      padding: 1em 0;
    }
  }
}

.nav--secondary {
  li {
    margin: 0 $space-quarter;
    a {
      color: $sky;
      font-size: 0.8125rem;
      font-weight: 400;
      text-transform: uppercase;
      letter-spacing: 1px;
    }
    .pill {
      color: $white;
    }
  }
}

.site-header__menus {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  align-items: flex-end;
}

.nav--secondary li a {
  padding: 0;
}

.nav--secondary li a.pill {
  color: $white;
  background-color: $sky;
  padding: 0.5em 1em;
}

.nav--secondary li a.pill--search:after {
  content: url('../imgs/production/magnifying-glass-white.svg');
  fill: $white;
  display: inline-block;
  margin-left: 7px;
  transform: translateY(2px);
}

.nav .dropdown {
  position: absolute;
  visibility: hidden;
  top: 100%;
  background-color: $sky;
  left: 0;
  right: 0;
  height: 100px;
  z-index: -1;
  box-shadow: inset 0px 5px 5px 0px rgba(0,0,0,0.15);
}

.nav .is-active .dropdown {
  visibility: visible;
}

.site-search {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  margin: auto;
  background-color: $sky;
  transform: translateY(-100%);
  transition: 0.3s transform ease-in-out;
}

.site-header.search-open .site-search {
  transform: translateY(0);
}




