.sidebar__ctas {
  position: relative;
}

.sidebar__ctas > li > a {
  display: block;
  width: 100%;
  padding-top: 100%;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  cursor: pointer;
  transform-origin: 50% 50%;
  transition: all 0.25s ease-in-out;
  backface-visibility: hidden;
  transform: translateZ(0);
  // -webkit-font-smoothing: subpixel-antialiased;
  /* You'll want to make a global timing function */
}

.sidebar__ctas > li > a:hover {
  z-index: 1;
  transform: scale(1.05);
}

.sidebar__ctas .cta__content {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  box-sizing: border-box;
  padding: rhythm(1);
  z-index: 1;
}

.sidebar__ctas .cta__content svg {
  fill: $white;
}

.sidebar__ctas .cta__content h4 {
  color: $white;
  font-family: $serif;
  -webkit-font-smoothing: subpixel-antialiased;
}

.sidebar__ctas .cta__content h4 > span {
  display: block;
  font-size: 0.625em;
}

.sidebar__ctas .cta__arrow {
  position: absolute;
  right: 0;
  bottom: 0;
  z-index: 2;
}

.sidebar__ctas .cta__arrow svg:first-child {
  fill: #ffffff;
  position: relative;
  width: 22px;
  z-index: 1;
  right: 15px;
  bottom: 15px;
}

.sidebar__ctas .cta__arrow svg:last-child {
  fill: $navy;
  opacity: 0.5;
  background-blend-mode: multiply;
  position: absolute;
  right: 0;
  bottom: 0;
}

.cta__overlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(6,92,147,0.7);
}

.cta--yellow .cta__overlay {
  background-color: rgba(208,152,0,0.7);
}

.sidebar__ctas .cta--yellow .cta__arrow svg:last-child {
  fill: #956b06;
}