// 1up Layout
.l-1up {
  
  .grid--2col & {
    min-width: 0;
    grid-column: 1 / 2;
    
    // For fallback, ensure you (1) change the display, 
    // (2) set a width, add (3) a 'grid-gap' with padding,
    // and for more complex layouts, (4) add clears & floats.
    .no-cssgrid & {
      display: block; // 1
      width: 100%; // 2
      padding-left: ($gap / 2); // 3
      padding-right: ($gap / 2); // 3
    }
    
    @include breakpoint($bp1) {
      grid-column: 1 / 3;
    }
  }
}
