// 2up Layout
.l-2up--1 {
  
  .grid--2col & {
    min-width: 0;
    grid-column: 1 / 2;
    
    // For fallback, ensure you (1) change the display, 
    // (2) set a width, add (3) a 'grid-gap' with padding,
    // and for more complex layouts, (4) add clears & floats.
    .no-cssgrid & {
      display: block; // 1
      width: 100%; // 2
      padding-left: ($gap / 2); // 3
      padding-right: ($gap / 2); // 3
    }
    
    @include breakpoint($bp1) {
      
      // For fallback, ensure you (1) change the display, 
      // (2) set a width, add (3) a 'grid-gap' with padding,
      // and for more complex layouts, (4) add clears & floats.
      .no-cssgrid & {
        width: 50%; // 2
        float: left; // 4
        clear: left; // 4
      } 
    }
  }
}

.l-2up--2 {
  
  .grid--2col & {
    min-width: 0;
    grid-column: 1 / 2;
    
    // For fallback, ensure you (1) change the display, 
    // (2) set a width, add (3) a 'grid-gap' with padding,
    // and for more complex layouts, (4) add clears & floats.
    .no-cssgrid & {
      display: block; // 1
      width: 100%; // 2
      padding-left: ($gap / 2); // 3
      padding-right: ($gap / 2); // 3
    }
    
    @include breakpoint($bp1) {
      grid-column: 2 / 3;
      
      // For fallback, ensure you (1) change the display, 
      // (2) set a width, add (3) a 'grid-gap' with padding,
      // and for more complex layouts, (4) add clears & floats.
      .no-cssgrid & {
        width: 50%; // 2
        float: right; // 4
      }
    }
  }
}
