button {
  display: inline-block;
  padding: 1px 56px 1px 30px;
  margin-bottom: 0;
  font-family: $serif;
  //.font-size(12);
  @include adjust-font-size-to(15px, 1.75);
  font-weight : bold;
  // line-height : (@baselineheight*.75);
  color: $navy;
  text-align: center;
  vertical-align: middle;
  background-color: transparent;
  border: 3px solid $navy;
  border-radius: 100px; /* to override inputs with .btn class */
  cursor: pointer;
  // *margin-left : .3em;
  // .transition(background-color .1s linear);

  background-image: url("../imgs/production/arrow-right.svg");
  background-repeat: no-repeat;
  background-position: center right 25px;
}